<template>
  <div class="app-container">
    <div class="filter-container">
      <el-select
        v-model="listQuery.accessPointTypeId"
        placeholder="类型"
        clearable
        class="filter-item"
        style="width: 130px"
      >
        <el-option v-for="item in codeOptions" :key="item.id" :label="item.name" :value="item.id" />
      </el-select>
      <el-input
        v-model="listQuery.name"
        placeholder="名称"
        style="width: 200px;"
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-button
        v-waves
        class="filter-item"
        type="primary"
        icon="el-icon-search"
        @click="handleFilter"
      >搜索</el-button>
    </div>
    <el-table
      v-loading="listLoading"
      :data="list"
      element-loading-text="Loading"
      stripe
      fit
      highlight-current-row
    >
      <el-table-column align="center" label="序号" width="75">
        <template slot-scope="scope">{{ scope.$index + 1 }}</template>
      </el-table-column>
      <el-table-column label="名称" align="center">
        <template slot-scope="scope">{{ scope.row.name }}</template>
      </el-table-column>
      <el-table-column label="地址">
        <template slot-scope="scope">/api/{{ scope.row.code }}/v1/{{ scope.row.id }}</template>
      </el-table-column>
      <el-table-column label="单价(元)" width="110" align="center">
        <template slot-scope="scope">
          <el-tag :type="scope.row.price | priceFilter" size="small">
            {{
            scope.row.price ? parseFloat(scope.row.price) / 100.0 : 0
            }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column class-name="status-col" label="状态" align="center">
        <template slot-scope="scope">{{ statusMap[scope.row.status] }}</template>
      </el-table-column>
      <el-table-column label="操作" align="center" class-name="small-padding fixed-width">
        <template slot-scope="{ row }">
          <el-button
            v-if="checkPermission(['ADMIN'])"
            type="primary"
            size="mini"
            icon="el-icon-edit"
            @click="handleUpdate(row)"
          >调整价格</el-button>
          <el-button
            v-if="checkPermission(['ADMIN'])"
            size="mini"
            @click="handleModifyStatus(row)"
          >{{ 1 === row.status ? "停用" : "开通" }}</el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination
      :total="total"
      :page.sync="listQuery.pageNo"
      :limit.sync="listQuery.pageSize"
      @pagination="fetchData"
    />
    <el-dialog title :visible.sync="dialogFormVisible">
      <el-form
        ref="dataForm"
        :rules="rules"
        :model="accessPoint"
        label-position="left"
        label-width="80px"
        style="margin-left:35px;"
      >
        <el-form-item label="名称" prop="name">
          <el-input v-model="accessPoint.name" readonly="readonly" />
        </el-form-item>
        <el-form-item label="单价(元)" prop="price">
          <el-input v-model="accessPoint.price" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取消</el-button>
        <el-button type="primary" @click="updateData()">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { list, grant, accessType } from "@/api/accessPoint";
import checkPermission from "@/utils/permission"; // 权限判断函数
import { isMoney } from "@/utils/validate";
import Pagination from "@/components/Pagination";
import waves from "@/directive/waves"; // waves directive

export default {
  components: { Pagination },
  directives: { waves },
  filters: {
    priceFilter(price) {
      if (price === undefined) {
        return "danger";
      }
      let val = "success";
      if (price) {
        if (price === 0) {
          val = "warning";
        } else if (price < 0) {
          val = "danger";
        }
      }
      return val;
    }
  },
  data() {
    const checkMoney = (rule, value, callback) => {
      if (value) {
        if (!isMoney(value)) {
          callback(new Error("金额错误"));
        } else {
          callback();
        }
      } else {
        callback(new Error("金额不能为空"));
      }
    };
    return {
      total: 0,
      codeOptions: [],
      listQuery: {
        tenantId: undefined,
        name: "",
        accessPointTypeId: undefined,
        pageNo: 1,
        pageSize: 20
      },
      accessPoint: {
        id: undefined,
        accessPointTypeId: undefined,
        name: "",
        tenantId: null,
        status: "1",
        price: 0.0
      },
      list: null,
      listLoading: true,
      textMap: {
        update: "编辑",
        create: "创建"
      },
      statusMap: {
        undefined: "停用",
        null: "停用",
        1: "正常",
        0: "停用"
      },
      dialogFormVisible: false,
      rules: {
        price: [{ required: true, validator: checkMoney, trigger: "change" }]
      }
    };
  },
  created() {
    accessType({ status: 1 }).then(response => {
      if (response.code === 0) {
        this.codeOptions = response.data.record;
        this.fetchData();
      } else {
        this.$message({
          message: response.message,
          type: "warning"
        });
      }
    });
  },
  methods: {
    checkPermission,
    fetchData() {
      this.listLoading = true;
      this.listQuery.tenantId = this.$route.params && this.$route.params.id;
      list(this.listQuery).then(response => {
        if (response.code === 0) {
          this.list = response.data.record;
          this.total = response.data.total;
        } else {
          this.$message({
            message: response.message,
            type: "warning"
          });
        }
        this.listLoading = false;
      });
    },
    handleFilter() {
      this.fetchData();
    },
    wrapper(row) {
      this.accessPoint.price =
        row.price === undefined ? "" : parseFloat(row.price) / 100.0;
      this.accessPoint.id = row.id;
      this.accessPoint.name = row.name;
      this.accessPoint.tenantId = this.listQuery.tenantId;
      this.accessPoint.accessPointTypeId = row.accessPointTypeId;
      this.accessPoint.status =
        row.status === undefined || row.status == null || row.status === 0
          ? 0
          : 1;
    },
    update(ap) {
      grant(ap).then(response => {
        if (response.code === 0) {
          this.fetchData();
          this.dialogFormVisible = false;
          this.$notify({
            title: "成功",
            message: "更新成功",
            type: "success",
            duration: 2000
          });
        }
      });
    },
    handleUpdate(row) {
      this.wrapper(row);
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
      this.dialogFormVisible = true;
    },
    handleModifyStatus(row) {
      let price = row.price;
      this.wrapper(row);
      if (!isMoney(row.price)) {
        this.$message({
          message: "价格配置错误",
          type: "warning"
        });
        return;
      }
      this.accessPoint.price = price;
      this.accessPoint.status =
        row.status === undefined || row.status == null || row.status === 0
          ? 1
          : 0;
      this.update(this.accessPoint);
    },
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          let ap = JSON.parse(JSON.stringify(this.accessPoint));
          if (ap.price) ap.price = ap.price * 100;
          this.update(ap);
        }
      });
    }
  }
};
</script>
